@use "sass:selector";
@use "./variables.scss" as *;
@use "./functions.scss" as *;
@use "./utils.scss" as *;

.navbar {
  border-bottom: 1px solid var(--light-clr);

  &-logo {
    padding: 1rem 0;
    padding-right: 1rem;

    font-size: 1.25rem;
  }

  &-toggler {
    display: none;
    appearance: none;

    padding: 0.25rem 0.75rem;

    cursor: pointer;

    border: 1px solid var(--light-clr);

    border-radius: 0.75rem;
    background: transparent;

    &::before {
      display: block;

      width: 30px;
      height: 30px;

      content: "";

      background-color: var(--light-clr);
      mask-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'><path stroke='black' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/></svg>");
    }

    @include shadowed_focus(--light-clr-half);

    @media ($mdeia-breakpoint) {
      display: block;
    }
  }

  &-collapse {
    display: flex;
    align-items: baseline;
    flex-basis: auto;
    flex-grow: 1;
    justify-content: space-between;

    @media ($mdeia-breakpoint) {
      display: block;
      overflow: hidden;
      flex-basis: 100%;

      max-height: 0;

      transition-property: max-height;

      &::before {
        display: block;

        width: 100%;
        height: 2px;

        content: "";

        background-color: var(--light-clr);
      }

      @include transition-base;
    }
  }

  .container {
    display: flex;
    align-items: baseline;
    flex-wrap: wrap;
    justify-content: space-between;

    @media ($mdeia-breakpoint) {
      align-items: center;
    }
  }

  .nav-list {
    display: flex;
    gap: 1rem;

    padding: 1rem 0;

    @media ($mdeia-breakpoint) {
      flex-direction: column;

      .nav-link {
        display: block;

        padding: 0.5rem;

        border-radius: 0.25rem;
        background-color: var(--bg-clr-darker);

        &:hover:not([aria-disabled="true"]) {
          background-color: var(--bg-clr-dark);
        }
      }
    }
  }

  @media ($mdeia-breakpoint) {
    &-toggler:checked + &-collapse {
      max-height: var(--height, max-content);
    }
  }
}
