:root {
  /* --primary */
  --primary: oklch(65% 50% 255deg / 100%);
  --primary-light: oklch(70% 50% 255deg / 100%);
  --primary-lighter: oklch(75% 50% 255deg / 100%);
  --primary-dark: oklch(60% 50% 255deg / 100%);
  --primary-darker: oklch(55% 50% 255deg / 100%);
  --primary-half: oklch(65% 50% 255deg / 50%);
  /* --secondary */
  --secondary: oklch(65% 0% 255deg / 100%);
  --secondary-light: oklch(70% 0% 255deg / 100%);
  --secondary-lighter: oklch(75% 0% 255deg / 100%);
  --secondary-dark: oklch(60% 0% 255deg / 100%);
  --secondary-darker: oklch(55% 0% 255deg / 100%);
  --secondary-half: oklch(65% 0% 255deg / 50%);
  /* --success */
  --success: oklch(65% 50% 140deg / 100%);
  --success-light: oklch(70% 50% 140deg / 100%);
  --success-lighter: oklch(75% 50% 140deg / 100%);
  --success-dark: oklch(60% 50% 140deg / 100%);
  --success-darker: oklch(55% 50% 140deg / 100%);
  --success-half: oklch(65% 50% 140deg / 50%);
  /* --danger */
  --danger: oklch(65% 50% 30deg / 100%);
  --danger-light: oklch(70% 50% 30deg / 100%);
  --danger-lighter: oklch(75% 50% 30deg / 100%);
  --danger-dark: oklch(60% 50% 30deg / 100%);
  --danger-darker: oklch(55% 50% 30deg / 100%);
  --danger-half: oklch(65% 50% 30deg / 50%);
  /* --warning */
  --warning: oklch(85% 50% 85deg / 100%);
  --warning-light: oklch(90% 50% 85deg / 100%);
  --warning-lighter: oklch(95% 50% 85deg / 100%);
  --warning-dark: oklch(80% 50% 85deg / 100%);
  --warning-darker: oklch(75% 50% 85deg / 100%);
  --warning-half: oklch(85% 50% 85deg / 50%);
  /* --dark */
  --dark: oklch(25% 0% 0deg / 100%);
  --dark-light: oklch(15% 0% 0deg / 100%);
  --dark-lighter: oklch(5% 0% 0deg / 100%);
  --dark-dark: oklch(35% 0% 0deg / 100%);
  --dark-darker: oklch(45% 0% 0deg / 100%);
  --dark-half: oklch(25% 0% 0deg / 50%);
  /* --light */
  --light: oklch(100% 0% 0deg / 100%);
  --light-light: oklch(105% 0% 0deg / 100%);
  --light-lighter: oklch(110% 0% 0deg / 100%);
  --light-dark: oklch(95% 0% 0deg / 100%);
  --light-darker: oklch(90% 0% 0deg / 100%);
  --light-half: oklch(100% 0% 0deg / 50%);
  /* --light-clr */
  --light-clr: oklch(65% 0% 255deg / 100%);
  --light-clr-light: oklch(70% 0% 255deg / 100%);
  --light-clr-lighter: oklch(75% 0% 255deg / 100%);
  --light-clr-dark: oklch(60% 0% 255deg / 100%);
  --light-clr-darker: oklch(55% 0% 255deg / 100%);
  --light-clr-half: oklch(65% 0% 255deg / 50%);
  /* --link-clr */
  --link-clr: oklch(55% 50% 250deg / 100%);
  --link-clr-light: oklch(65% 50% 250deg / 100%);
  --link-clr-lighter: oklch(75% 50% 250deg / 100%);
  --link-clr-dark: oklch(45% 50% 250deg / 100%);
  --link-clr-darker: oklch(35% 50% 250deg / 100%);
  --link-clr-half: oklch(55% 50% 250deg / 50%);
}

