.btn {
  padding: 0.5rem 1rem;
  cursor: pointer;
  border: none;
  border-radius: 0.75rem;
  transition: box-shadow 0.25s ease;
}
.btn:hover {
  background-color: var(--bg-clr-light);
}
.btn:active {
  background-color: var(--bg-clr-dark);
}
.btn:focus {
  box-shadow: 0 0 0 5px var(--bg-clr-half);
}

.nav-list {
  margin: 0;
  list-style: none;
}
.nav-link {
  padding: 1rem 0.25rem;
  text-decoration: none;
  color: var(--link-clr-darker);
  --link-clr: var(--text-clr);
  --link-clr-light: var(--text-clr-light);
  --link-clr-lighter: var(--text-clr-lighter);
  --link-clr-dark: var(--text-clr-dark);
  --link-clr-darker: var(--text-clr-darker);
  --link-clr-half: var(--text-clr-half);
}
.nav-link.active {
  color: var(--link-clr-lighter);
}

.navbar {
  border-bottom: 1px solid var(--light-clr);
}
.navbar-logo {
  padding: 1rem 0;
  padding-right: 1rem;
  font-size: 1.25rem;
}
.navbar-toggler {
  display: none;
  appearance: none;
  padding: 0.25rem 0.75rem;
  cursor: pointer;
  border: 1px solid var(--light-clr);
  border-radius: 0.75rem;
  background: transparent;
  transition: box-shadow 0.25s ease;
}
.navbar-toggler::before {
  display: block;
  width: 30px;
  height: 30px;
  content: "";
  background-color: var(--light-clr);
  mask-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'><path stroke='black' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/></svg>");
}
.navbar-toggler:focus {
  box-shadow: 0 0 0 5px var(--light-clr-half);
}
@media (width <= 768px) {
  .navbar-toggler {
    display: block;
  }
}
.navbar-collapse {
  display: flex;
  align-items: baseline;
  flex-basis: auto;
  flex-grow: 1;
  justify-content: space-between;
}
@media (width <= 768px) {
  .navbar-collapse {
    display: block;
    overflow: hidden;
    flex-basis: 100%;
    max-height: 0;
    transition-property: max-height;
    transition-timing-function: ease;
    transition-duration: 350ms;
  }
  .navbar-collapse::before {
    display: block;
    width: 100%;
    height: 2px;
    content: "";
    background-color: var(--light-clr);
  }
}
.navbar .container {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (width <= 768px) {
  .navbar .container {
    align-items: center;
  }
}
.navbar .nav-list {
  display: flex;
  gap: 1rem;
  padding: 1rem 0;
}
@media (width <= 768px) {
  .navbar .nav-list {
    flex-direction: column;
  }
  .navbar .nav-list .nav-link {
    display: block;
    padding: 0.5rem;
    border-radius: 0.25rem;
    background-color: var(--bg-clr-darker);
  }
  .navbar .nav-list .nav-link:hover:not([aria-disabled=true]) {
    background-color: var(--bg-clr-dark);
  }
}
@media (width <= 768px) {
  .navbar-toggler:checked + .navbar-collapse {
    max-height: var(--height, max-content);
  }
}

/* index.scss */
html,
body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

* {
  box-sizing: border-box;
  padding: 0;
  color: var(--text-clr);
}
*[aria-disabled=true] {
  cursor: not-allowed;
  text-decoration: line-through;
  color: var(--text-clr-half);
}
*[aria-disabled=true]:active {
  pointer-events: none;
}

a {
  color: var(--link-clr);
}